* {
  box-sizing: border-box;
  margin: 0;
}

body {
  color: #fff;
  background: #000;
}

.js {
  display: none;
}

.wrapper {
  text-align: center;
  width: 100%;
  height: 100%;
  position: absolute;
}

.align-child {
  vertical-align: middle;
  display: inline-block;
  position: relative;
}

.align-helper {
  vertical-align: middle;
  width: 0;
  height: 100%;
  display: inline-block;
}

canvas {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

#trigger {
  cursor: pointer;
}

#logo, #trigger {
  transition: opacity .1s;
}

#logo:hover, #trigger:hover, .translucent span:hover {
  opacity: 1 !important;
}

p, article {
  color: #fff;
  text-align: justify;
  word-break: break-word;
  max-width: 52ch;
  font-family: PT Sans, Noto Sans, Segoe UI, Roboto, Verdana, Helvetica, Arial, sans-serif;
}

p a, article a {
  color: #fff;
  text-decoration: underline;
}

.card {
  background: #0006;
  padding: 2ch;
}

#trigger, .translucent span {
  opacity: .42;
}
